@import url("https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;600;700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*============= Body conatiner ===============*/
body {
  background: rgb(240, 240, 240);
  font-family: "Baloo Tamma 2", sans-serif;
}
h2 {
  font-size: 1.2em;
}
.container {
  text-align: center;
  width: 95%;
  max-width: 1100px;
  margin-top: 10px;
  margin-bottom: 0;
  padding: 20px 10px;
}
/*============= Body conatiner ===============*/

/*============= App Header ===============*/
.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.App-header h1 {
  font-weight: 600;
  margin-top: 5px;
  font-size: 1.5em;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}
.App-link {
  color: #61dafb;
}
/*============= App Header ===============*/

/*============= SVG ===============*/
svg {
  width: 100%;
}
/*=============SVGr ===============*/

/*============= Country/Global data conatiner ===============*/
.data-container {
  display: flex;
  justify-content: center;
}
.global-case span {
  color: green;
  font-size: 0.9em;
}
.global-case h2 {
  margin: 0;
  margin-top: 20px;
  font-weight: 600;
}
.global-data {
  margin: 20px 10px;
  min-width: 150px;
  border-radius: 10px;
  background: rgb(260, 260, 260);
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
}
.global-data > div {
  padding: 10px;
}
.global-data h4 {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 1em;
}
/*============= Country/Global data conatiner ===============*/

/*============= Country wise Report ===============*/
.data-header {
  font-size: 1em;
  margin: 10px auto;
  font-weight: 600;
}
.flex-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}
.data-confirmed {
  max-height: 30vh;
  background: rgb(260, 260, 260);
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
  -webkit-overflow-scrolling: touch;
}
.data-confirmed h3 {
  background-color: rgba(0, 0, 0, 0.03);
  padding: 10px;
  font-weight: 600;
  font-size: 1em;
  width: 100%;
  margin: 0 auto;
}
.data-confirmed ul {
  list-style: none;
  margin: 0;
  overflow-y: scroll;
}
.data-confirmed li {
  text-align: start;
  padding: 10px;
  border-bottom: 2px solid gray;
}
.data-confirmed li:last-child {
  border-bottom: none;
}
.data-confirmed li p {
  margin: 0;
}
.data-confirmed li .confirmed {
  display: inline-block;
  color: red;
  margin-right: 2px;
}
/*============= Country wise Report ===============*/

/*============= Pie Cahrt ===============*/
.pie-chart {
  width: 100%;
}
.piechart-info {
  bottom: 0;
  right: 20%;
}
.piechart-container .btn {
  color: white;
  padding: 3px 3px;
  border-radius: 5px;
  margin-right: 5px;
  display: inline-block;
}
.piechart-container p {
  margin: 0;
  font-size: 0.7em;
}
.piechart-container .confirmed {
  background-color: #f94f4f;
  font-weight: 500;
}
.piechart-container .deaths {
  background: black;
}
.piechart-container .recovered {
  background: green;
}
.piechart-container .active {
  background: #17a2b8;
}
/*============= Pie Cahrt ===============*/

/*============= symptoms ===============*/
.symptoms {
  text-align: start;
  width: 100%;
  padding: 0;
}
.symptoms ul {
  margin-left: 20px;
}
/*============= symptoms ===============*/

/*============= Statewise Report Table ===============*/
abbr[title] {
  text-decoration: none;
}
.table thead th {
  vertical-align: baseline;
}
.badge {
  font-weight: 500;
}
.badge-warning {
  background-color: #f94f4f;
  color: white;
}
.badge-success {
  background-color: #17a2b8;
}
.badge-dark {
  background-color: green;
}
.badge-danger {
  background-color: black;
}
.table {
  position: relative;
}
.table tbody td .deltas {
  display: inline-block;
  font-size: 11px !important;
  padding: 2px;
  margin-right: 2px;
}

.table tbody td .deltas svg {
  width: 9px;
  height: 9px;
  stroke-width: 3;
  vertical-align: -0.25px;
}

.table tbody td {
  padding: 0.25rem;
  border-radius: 5px;
  text-align: right;
}

.table thead th.sticky {
  background-color: #a8acb0b8;
  position: sticky;
  padding: 3px;
}


.table tbody .state-last-update td .last-update {
  flex-direction: row;
  padding: 5px;
  align-items: baseline;
  color: #28a745 !important;
  margin-top: 20px;
}


.table tbody .state-last-update td .last-update > h6 {
  display: inline;
  font-size: 13px;
  font-weight: 600;
}

.table tbody .district-heading td {
  font-size: 0.75rem;
  font-weight: 900;
  padding: 0.45rem;
  background: rgba(108, 117, 125, 0.0627451);
}

table .dropdown {
  display: inline-block;
  position: absolute;
  left: -1.25rem;
  background: rgba(108, 117, 125, 0.0627451);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  cursor: pointer;
}
.table tbody td {
  vertical-align: bottom;
}

.heading-content .arrow-down,
.heading-content .arrow-up {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  display: inline-block;
  margin-left: 5px;
}

.arrow-down {
  border-top: 5px solid black;
}

.arrow-up {
  border-bottom: 5px solid black;
}

.rotateDownRight {
  animation-name: rotateDownRight;
  animation-duration: 0.25s;
  animation-fill-mode: both;
}
.rotateRightDown {
  animation-name: rotateRightDown;
  animation-duration: 0.25s;
  animation-fill-mode: both;
}

@keyframes rotateDownRight {
  100% {
    transform: rotate(-90deg);
  }
}
@keyframes rotateRightDown {
  0% {
    transform: rotate(-90deg);
  }
}

table .dropdown:hover {
  transform: scale(1.2);
}

table tbody td:first-child {
  text-align: left;
  max-width: 7.5rem;
  word-wrap: break-word;
  vertical-align: middle;
}

.table-fineprint {
  text-align: right;
  margin: 0 0.25rem 0.25rem 0;
  font-weight: 600;
  font-size: 13px;
}
/*============= Statewise Report Table ===============*/

/*============= NewsHighlights ===============*/
.news-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  padding-bottom: 25px;
  text-align: start;
}
.news-container > ul {
  margin: 10px;
  padding: 10px;
  background: rgb(260, 260, 260);
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
  border-radius: 5px;
}
.news-container img {
  width: 100%;
}
.news-container h4 {
  font-size: 1em;
  color: #0067b8;
  margin-top: 5px;
}
.news-container p {
  font-size: 0.9em;
  font-weight: 400;
  margin: 0;
}
.news-container a {
  display: inline-block;
  color: #0067b8;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  padding: 5px;
  text-decoration: none;
}
.news-container > ul a:hover {
  border: 1px solid #0067b8;
  border-radius: 5px;
  color: black;
}
.title h4 {
  margin: 10px auto;
  background: #fff;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
  border-radius: 5px;
}
/*============= NewsHighlights ===============*/

/*============= TodayStatus ===============*/
.today-alert {
  padding: 0;
  margin: 10px auto;
  width: 100%;
}
.alert-heading {
  font-size: 1.2em;
  text-decoration: underline;
  font-weight: 600;
}
.today-alert p {
  font-size: 0.9em;
  margin: 0;
}
.google-pay a {
  font-size: 0.9em;
}
/*============= TodayStatus  ===============*/

/*============= Footer ===============*/
.footer-container {
  padding: 0;
  margin: 0;
}
.nav-bar .social {
  border-bottom: 2px solid #0067b8;
}
/*============= Footer ===============*/

/*============= Barchart ===============*/
.barchart {
  margin-top: 20px;
  border-radius: 5px;
  background-color: #fff;
}
/*============= Barchart ===============*/

/* ============ Apexcharts ========== */
.chart-container {
  margin: 30px auto;
  border-radius: 5px;
  background-color: #fff;
  display: flex;
}

#chart {
  margin-bottom: 20px;
  padding: 20px;
  width: 50%;
}
.test5 .apexcharts-legend-series {
  text-align: start;
}

.test5 {
  padding: 10px;
  width: 100%;
  height: 90%;
}

.chart-container h4 {
  text-align: center;
  font-size: 1.2em;
}
/* ============ Apexcharts ========== */

/* Animation  */
/* Animation Support */
.fadeInUp {
  animation-name: fadeInUp;
  animation-duration: 0.8s;
  animation-fill-mode: both;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ====== Timer-Series========  */
.mapdata-container {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.header {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.MapExplorer .header {
  color: #6c757d;
  align-self: flex-start;

  margin: 1rem 0;
}
.MapExplorer .header h1 {
  font-size: 1.2rem;
}
.MapExplorer .header > * {
  font-weight: 600;
}

.MapExplorer,
.MapExplorer .header {
  display: flex;
  flex-direction: column;
}

.MapExplorer .map-stats {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
}
.MapExplorer .map-stats .stats {
  margin: 5px 5px;
  min-width: 100px;
  height: 60px;
  padding: 2px;
  width: 100px;
  border-radius: 10px;
  border-radius: 10px;
  background: rgb(260, 260, 260);
  box-shadow: 0 1px 3px 0 #d4d4d5;
}
.MapExplorer .map-stats h1,
.MapExplorer .map-stats h3,
.MapExplorer .map-stats h5 {
  font-size: 0.9rem;
}

.MapExplorer .map-stats h1 {
  margin: 0;
}
.MapExplorer .map-stats h6 {
  font-size: 0.8rem;
}
.MapExplorer .map-stats h5 {
  font-weight: 600;
  margin-bottom: 5px;
  font-size: 0.75rem;
}
.MapExplorer .map-stats .stats.tested svg {
  position: absolute;
  right: 0.35rem;
  top: 0;
  stroke-width: 3;
  width: 12px;
  cursor: pointer;
  transition: stroke 0.2s ease-in-out;
}
/* ====== Timer-Series========  */

/* ==========MAP Explorer - SVG ========= */
.MapExplorer .svg-parent {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 5px;
  overflow: hidden;
}
.MapExplorer .svg-parent svg {
  margin: 0 auto;
  height: 100%;
  width: 100%;
  align-self: center;
}
.MapExplorer .svg-parent #chart {
  z-index: 10;
  width: 90%;
}
.MapExplorer {
  width: 95%;
  margin: 10px 0;
  padding: 5px 0;
  margin-right: 5px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
  background-color: #fff;
  border-radius: 5px;
}
.MapExplorer .meta {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding: 5px;
  margin-bottom: 0;
}

.MapExplorer .meta h2 {
  color: #e23028;
  word-wrap: break-word;
  font-weight: 900;
}
.last-update {
  display: flex;
  flex-direction: column;
  align-self: center;
  text-align: right;
  margin-bottom: auto;
}
.last-update h6 {
  color: rgba(40, 167, 69, 0.6);
  font-weight: 900;
  font-size: 0.8rem;
  margin: 0;
}
.last-update h3 {
  color: #28a745;
  font-weight: 600;
  font-size: 0.8rem;
}
.MapExplorer .meta .district-confirmed {
  color: #e23028;
  font-size: 1rem;
  position: absolute;
  font-weight: 900;
  top: 2rem;
  margin: 0;
  text-align: start;
  line-height: 15px;
}
.MapExplorer .back-button {
  padding: 4px;
  border-radius: 5px;
  background: rgba(255, 193, 7, 0.12549);
  color: #fd7e14;
  transition: all 0.2s ease-in-out;
}
.MapExplorer .meta .unknown {
  position: absolute;
  font-size: 0.9rem;
  top: 2.5rem;
  width: 30%;
  right: 0;
  text-align: right;
  font-weight: 600;
  font-weight: 0.75rem;
}
.back-button {
  z-index: 11;
}
.MapExplorer .legend {
  margin-top: 1em;
  width: 100%;
}
.map-hover {
  stroke: #ff073a;
  stroke-width: 2;
}
/* ==========MAP Explorer ========= */

/* Deep Dive */
.cards-container {
  margin: 20px 0;
}
.cards-container .cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.cards-container .cards .card {
  margin-bottom: 1em;
  width: 48%;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
  height: 30rem;
}

.cards-container .cards .card .charts-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.cards-container .cards .card .charts-header .chart-title {
  font-size: 0.9rem !important;
  padding: 10px 0;
}

.chart-note {
  font-size: 0.9rem;
}
.cards-container .cards .card .charts-header .chart-content {
  height: 100%;
  width: 100%;
}
.chart-content .chartjs-render-monitor {
  width: 98% !important;
  height: 98% !important;
  margin: 0 auto;
}

.chartjs-size-monitor,
.chartjs-size-monitor-expand,
.chartjs-size-monitor-shrink {
  position: absolute;
  direction: ltr;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  pointer-events: none;
  visibility: hidden;
  z-index: -1;
}

@keyframes chartjs-render-animation {
  0% {
    opacity: 0.99;
  }
  100% {
    opacity: 1;
  }
}

/* =======Spread Trends ======= */
.timeseries-header {
  width: 95%;
  margin: 10px 0;
  margin-left: 5px;
  padding: 10px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
  background-color: #fff;
  border-radius: 5px;
}

.timeseries-header h1 {
  font-size: 1.2rem;
  margin: 1rem 0;
  text-align: center;
  color: #6c757d;
  font-weight: 600;
}
.tabs {
  position: relative;
  z-index: 99;
  display: flex;
  flex-direction: row;
}

.tabs .tab.focused {
  opacity: 1.2;
}
.tabs .tab {
  padding: 3px;
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  cursor: pointer;
  text-align: center;
  margin-right: 0.25rem;
  transition: all 0.3s ease-in-out;
  opacity: 0.5;
}
.tabs .tab h4 {
  font-size: 0.9rem;
  margin: 0;
  font-weight: 500;
  opacity: 0.9;
}

.timeseries-header .scale-modes {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  bottom: 0;
  right: 0;
  z-index: 99;
  color: #6c757d;
  margin-top: 1rem;
}

.timeseries-header .scale-modes label {
  font-size: 0.75rem;
  font-weight: 900;
  z-index: 99;
  color: black;
  transition: all 0.2s ease-in-out;
}
.timeseries-header .scale-modes > * {
  align-self: center;
}
.timeseries-header .timeseries-logmode,
.timeseries-header .timeseries-mode {
  margin-left: 1rem;
  display: flex;
  flex-direction: row;
}

.timeseries-header .timeseries-logmode label,
.timeseries-header .timeseries-mode label {
  font-size: 0.75rem;
  margin-right: 0.2rem;
  padding-bottom: 0;
  color: #6c757d;
}

.timeseries-header .timeseries-logmode input.switch:checked,
.timeseries-header .timeseries-mode input.switch:checked {
  background-color: rgba(108, 117, 125, 0.6);
  transition: all 0.2s ease-in-out;
}
.timeseries-header .timeseries-logmode input.switch,
.timeseries-header .timeseries-mode input.switch {
  position: relative;
  appearance: none;
  outline: none;
  margin: 0;
  cursor: pointer;
  width: 24px;
  height: 14px;
  background-color: #fff;
  border: 2px solid #d9dadc;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}
.timeseries-header .timeseries-logmode input.switch:checked:after,
.timeseries-header .timeseries-mode input.switch:checked:after {
  left: 10px;
  background-color: #f8f9fa;
}
input.switch:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.timeseries-header .timeseries-logmode input.switch:after,
.timeseries-header .timeseries-mode input.switch:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: rgba(108, 117, 125, 0.6);
  border-radius: 50%;
}
input.switch:checked:after {
  left: 10px;
  background-color: #f8f9fa;
  transition: all 0.3s ease-in-out;
}
input.switch:after,
input.switch:checked {
  background-color: rgba(108, 117, 125, 0.6);
  transition: all 0.3s ease-in-out;
}

label {
  cursor: default;
}

/* ========= */

.TimeSeries-Parent {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-self: center;
}
.Minigraph,
.timeseries {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  width: 100%;
}

.Minigraph .stats,
.timeseries .stats {
  padding: 0.25rem;
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.Minigraph .svg-parent,
.timeseries .svg-parent {
  position: relative;
  display: flex;
  width: 100%;
  align-self: center;
  background: rgba(255, 7, 58, 0.12549);
  border-radius: 5px;
  margin-bottom: 1rem;
  height: 10rem;
}

.trends-state-name select {
  appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, grey 0),
    linear-gradient(135deg, grey 50%, transparent 0);
  background-position: calc(100% - 13px) 50%, calc(100% - 8px) 50%;
  background-size: 5px 5px, 5px 5px;
  background-repeat: no-repeat;
  border: 2px solid #e8e8e9;
  border-radius: 10px;
  font-weight: 900;
  padding: 5px;
  color: #6c757d;
}
.trends-state-name {
  text-align: start;
}

.Minigraph .stats h2,
.Minigraph .stats h5.title,
.Minigraph .stats h6,
.timeseries .stats h2,
.timeseries .stats h5.title,
.timeseries .stats h6 {
  font-size: 12px;
}
.Minigraph .stats h2,
.Minigraph .stats h5,
.Minigraph .stats h6,
.timeseries .stats h2,
.timeseries .stats h5,
.timeseries .stats h6 {
  margin: 0;
  font-size: 12px;
  color: #ff073a;
  font-weight: 600;
  transition: all 0.15s ease-in-out;
}

.timeseries .stats.is-blue > *,
.timeseries .stats.is-blue .stats-bottom > * {
  color: #007bff;
}
.timeseries .stats.is-green > *,
.timeseries .stats.is-green .stats-bottom > * {
  color: #28a745;
}
.timeseries .stats.is-gray > *,
.timeseries .stats.is-gray .stats-bottom > * {
  color: #6c757d;
}

.timeseries .stats .stats-bottom h2 {
  font-size: 1rem;
  margin-right: 2px;
}
.timeseries .stats .stats-bottom h6 {
  font-size: 0.7rem;
}
.timeseries .stats .stats-bottom > * {
  display: inline-block;
}

.Minigraph .svg-parent svg,
.timeseries .svg-parent svg {
  width: 100%;
}
.Minigraph .svg-parent.is-blue,
.timeseries .svg-parent.is-blue {
  background: rgba(0, 123, 255, 0.0627451);
}
.Minigraph .svg-parent.is-green,
.timeseries .svg-parent.is-green {
  background: rgba(40, 167, 69, 0.12549);
}
.Minigraph .svg-parent.is-gray,
.timeseries .svg-parent.is-gray {
  background: rgba(108, 117, 125, 0.0627451);
}
.pills {
  align-self: flex-end;
}

.pills > button {
  border: 2px solid rgba(255, 193, 7, 0.12549);
  border-top: 0;
  border-bottom: 0;
  background-color: rgba(255, 193, 7, 0.12549);
  color: #fd7e14;
  font-size: 12px;
  text-align: center;
  font-weight: 900;
  transition: background 0.25s ease-in-out;
  padding: 5px;
  cursor: pointer;
  margin: 0;
  outline: none;
}

.pills > button:first-of-type {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border: 0;
}

.pills > button.selected {
  background-color: rgba(255, 193, 7, 0.5);
}

